import { createApp } from 'vue';
import { FontAwesomeIcon } from '@fortawesome/vue-fontawesome';
import { library } from '@fortawesome/fontawesome-svg-core';
import { faGraduationCap, faEnvelopeSquare, faPhoneSquareAlt } from '@fortawesome/free-solid-svg-icons';
import { faFacebookSquare, faLinkedin, faGithubSquare } from '@fortawesome/free-brands-svg-icons';
import App from './App.vue';

library.add(faGraduationCap);
library.add(faEnvelopeSquare);
library.add(faPhoneSquareAlt);
library.add(faFacebookSquare);
library.add(faGithubSquare);
library.add(faLinkedin);

createApp(App).component('font-awesome-icon', FontAwesomeIcon).mount('#app');
